import { Box, Typography } from '@mui/material'
import React from 'react';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import GitHubIcon from '@mui/icons-material/GitHub';
import YouTubeIcon from '@mui/icons-material/YouTube';

const Footer = () => {
    return (
        <>
            <Box sx={{ textAlign: 'center', bgcolor: '#f57c00', fontWeight: '600', color: '#fff', py: 2, px: 0 }}>

                <Box sx={{my: 1, "& svg": {
                    fontSize: '40px', mr: 0, cursor: 'pointer'}, "& svg:hover": 
                    { color: '#000', transform: 'translateX(5px)', transition: 'all 400ms'}
                    }} >
                    {/* icons section  */}
                    <InstagramIcon />
                    <TwitterIcon />
                    <GitHubIcon />
                    <YouTubeIcon />
                </Box>
                <Typography variant='h6' sx={{
                    "@media (max-width: 600px)": {
                        fontSize: '1rem'
                    }
                }} >
                    All Right Reserved &copy; SaveDev Technology
                </Typography>
            </Box>
        </>
    )
}

export default Footer