import React from 'react';
import { Box, Typography, AppBar, Toolbar, IconButton, Drawer, Divider } from '@mui/material';
import FoodBankRoundedIcon from '@mui/icons-material/FoodBankRounded';
import { Link, NavLink } from 'react-router-dom';
import '../../styles/Header.css';
import MenuRoundedIcon from '@mui/icons-material/MenuRounded';
import { useState } from 'react';

const Header = () => {


    const [mobileOpen, setMobileOpen] = useState(false);

    // handling drawer menu toggle 
    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen)
    }
    const drawer = (
        <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>


            <Typography color='#000' variant='h6' component={'div'}
                sx={{ flexGrow: 1 , my: 1}}>
                <FoodBankRoundedIcon />
                Curry Town
            </Typography>

            {/* // menu items for mobile drawer view */}
            <Divider />
            <ul className='mobile-menu'>
                <li>
                    <Link to='/'>Home</Link>
                </li>
                <li>
                    <Link to='/menu'>Menu</Link>
                </li>
                <li>
                    <Link to='/about'>About</Link>
                </li>
                <li> <Link to='/contact'>Contact</Link></li>
            </ul>

        </Box>
    )
    return (
        <>
            <Box>
                <AppBar component={'nav'} sx={{ bgcolor: '#f57c00', m: 0 }}>
                    <Toolbar >
                        {/* menuicon button added  */}
                        <IconButton color='inherit' aria-label='open drawer' edge='start'
                            sx={{ mr: 1, display: { sm: 'none' } }} onClick={handleDrawerToggle}>
                            <MenuRoundedIcon />
                        </IconButton >

                        <Typography color='#000' variant='h5' component={'div'}
                            sx={{ flexGrow: 1 }}>
                            <FoodBankRoundedIcon />
                            Curry Town
                        </Typography>
                        {/* // menu items */}
                        <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
                            <ul className='nav-menu'>
                                <li>
                                    <NavLink to='/' className={({ isActive }) => (isActive ? 'active' : '')}>Home</NavLink>
                                </li>
                                <li>
                                    <NavLink to='/menu'>Menu</NavLink>
                                </li>
                                <li>
                                    <NavLink to='/about'>About</NavLink>
                                </li>
                                <li> <NavLink to='/contact'>Contact</NavLink></li>
                            </ul>
                        </Box>
                    </Toolbar>
                </AppBar>
                <Box component='nav' >
                    <Drawer variant='temporary' open={mobileOpen} onClose={handleDrawerToggle}
                        sx={{
                            display: { xs: 'block', sm: 'none' }, "& .MuiDrawer-paper":
                                { boxSizing: "border-box", width: '240px' }
                        }}>
                        {drawer}
                    </Drawer>
                </Box>
                <Box >

                <Toolbar />
                </Box>
            </Box>
        </>
    )
}

export default Header