import React from 'react'
import Layout from '../components/layout/Layout'
import { Box, Typography } from '@mui/material'

const About = () => {
  return (
    <Layout>
      <Box sx={{ my: 15, textAlign: 'center', p: 3,  "& h4": { fontWeight: 'bold', my: 2 }, "&body1": {textAlign: 'justify'}, "@media (max-width:600px)": {
        mt: 0,
        "& h4": { fontSize: '1.2rem' }, "&body1": {fontSize: '0.8rem'}
      }}}
      >
        <Typography variant='h4'  >
          Welcome to Curry Town
        </Typography>
        <Typography variant='body1' >

          The challenge that restaurants have is that people come in, eat, pay, and leave. Theres little opportunity to build relationships and invite diners back.

          Thats where texting comes in. When you collect phone numbers you have a connection to your customers that you can use to drive sales and increase customer lifetime value.

          The most common way restaurants drive repeat business using phone numbers is by sending time-sensitive promotions that often lead to immediate sales.
        </Typography>
        <br/>
        <Typography variant='body1' >
          Restaurant marketing has become much more complicated in the past few years. Competition is heating up and people looking to fill their stomachs now have an array of possible options.

          Its not just the number of eateries that have increasedtheres been an explosion in the number of marketing channels vying for customers attention.

          Its now common for restaurants to promote themselves on social media, email, search engines and review sites. All of those channels have their benefits, but none benefit from text marketings unparalleled:
        </Typography>
      </Box>
    </Layout>
  )
}

export default About