import React from 'react'
import Layout from '../components/layout/Layout'
import { Box, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@mui/material';
import SupportAgentIcon from '@mui/icons-material/SupportAgent';
import MailIcon from '@mui/icons-material/Mail';
import CallIcon from '@mui/icons-material/Call';

const Contact = () => {
  return (
    <Layout>
      <Box sx={{
        my: 10, "& h5": { fontWeight: 'bold', my: 2, mx: 5}

      }}>
        <Typography variant='h5'>Contact Us</Typography>
        <Typography variant='body1' sx={{ mx: 5 }}>
          It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English.
        </Typography>
      </Box>
      <Box sx={{m: 4, width: '60%', ml: 5, "@media (max-width:600px)": { textAlign: 'center'}}}
      >
        <TableContainer component={Paper}>
          <Table aria-label='contact table'>
            <TableHead>
              <TableRow>
                <TableCell sx={{bgcolor: '#000', color: '#fff'}} align='center'>Contact Detail</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell>
                  <SupportAgentIcon sx={{ pt: 1, color: 'red' }} /> 1234-5678-1010 (Tollfree)
                </TableCell>
              </TableRow>

              <TableRow>
                <TableCell>
                  <MailIcon sx={{ pt: 1, color: 'blue' }} /> help@currytown.com
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell>
                  <CallIcon sx={{ color: 'green', pt: 1 }} /> 9090901111
                </TableCell>
              </TableRow>

            </TableBody>
          </Table>
        </TableContainer >
      </Box>
    </Layout>
  )
}

export default Contact