import React from 'react'
import Layout from '../components/layout/Layout'
import { Box, Card, CardActionArea, CardContent, CardMedia, Typography } from '@mui/material'
import { menuList } from '../data/Data'

const Menu = () => {
  return (
    <Layout>
      <Box sx={{display: 'flex', flexWrap: 'wrap', justifyContent: 'center'}}>
        {
          menuList.map(menu => (
            <Card sx={{maxWidth: '40%', m:2, }}>
              <CardActionArea>
                <CardMedia src={menu.img} alt={menu.name} sx={{}} component={'img'} />

                <CardContent>
                  <Typography variant='h5' component={'div'} gutterBottom sx={{"@media (max-width:600px)":{fontSize: '1rem', fontWeight: 'bold'}}} >{menu.name}</Typography>
                  <Typography variant='body2'>{menu.description} </Typography>
                </CardContent>
              </CardActionArea>

            </Card>
          ))
        }
      </Box>
    </Layout>
  )
}

export default Menu